import React, { useState } from "react";
import "./InfoCard.css";
import { UilPen } from "@iconscout/react-unicons";
import ProfileModal from "../ProfileModal.jsx/ProfileModal";
import { useSelector,useDispatch } from "react-redux";
import { logOut } from "../../Store/UserActions";

const InfoCard = () => {
  const [modalOpened, setModalOpened] = useState(false);
  const {user} = useSelector((state)=>state.user);
  const dispatch=useDispatch();
  const loggingOut = ()=>{
          dispatch(logOut());
  };
  return (
    <div className="InfoCard">
      <div className="infoHead">
        <h4>Your Info</h4>
        <div>
          <UilPen
            width="2rem"
            height="1.2rem"
            onClick={() => setModalOpened(true)}
          />
          <ProfileModal
            modalOpened={modalOpened}
            setModalOpened={setModalOpened}
          />
        </div>
      </div>

      <div className="info">
        <span>
          <b>Status </b>
        </span>
        <span>{user.about?user.about.relation?user.about.relation:"":""}</span>
      </div>

      <div className="info">
        <span>
          <b>Lives in </b>
        </span>
        <span>{user.about?user.about.livesIn?user.about.livesIn:"":""}</span>
      </div>

      <div className="info">
        <span>
          <b>Works at </b>
        </span>
        <span>{user.about?user.about.workAt?user.about.workAt:"":""}</span>
      </div>

      <div className="info">
        <span>
          <b>Country </b>
        </span>
        <span>{user.about?user.about.country?user.about.country:"":""}</span>
      </div>


      <button className="button logout-button" onClick={loggingOut}>Logout</button>
    </div>
  );
};

export default InfoCard;
