import React,{useRef,useEffect} from "react";
import "./Auth.css";
import Logo from "../../img/logo.png";
import { useSelector,useDispatch } from "react-redux";
import { useNavigate,useLocation,Link } from "react-router-dom";
import { userActions } from "../../Store/UserReducer";
import { loggedIn } from "../../Store/UserActions";
import { ToastContainer, toast } from 'react-toastify';
  import 'react-toastify/dist/ReactToastify.css';

const Auth = () => {
  const dispatch = useDispatch();
  const email = useRef();
  const password = useRef();
  const navigate = useNavigate();
  const location=useLocation();
  const url= new URLSearchParams(location.search).get('redirect')||'profile';

  const {loading, error, isAuthenticated } = useSelector((state) => state.user);

  const notify = (error) => toast.error(error);

  
  useEffect(() => {
    if (error) { //if error come
      notify(error);
      dispatch(userActions.clearError());// for clearing the error
    };
    if (isAuthenticated) {

      navigate(`/${url}`);
    }
  }, [isAuthenticated, navigate, error,dispatch,url]);

  //handling the logIn form 
  const submitHandler = (e) => {
    e.preventDefault();
    let data = { email: email.current.value, password: password.current.value};
    dispatch(loggedIn(data));
    email.current.value = "";
    password.current.value = "";
  }
  return (
    <div className="Auth">
      <div className="a-left">
        <img src={Logo} alt="" />
        <div className="Webname">
          <h1>ZKC Media</h1>
          <h6>Explore the ideas throughout the world</h6>
        </div>
      </div>
      {/* logIn */}
      <div className="a-right">
        <form className="infoForm authForm" onSubmit={submitHandler}>
          <h3>Log In</h3>
  
          <div>
            <input
              type="text"
              placeholder="Enter Email"
              className="infoInput"
              name="username"
              ref={email}
            />
          </div>
  
          <div>
            <input
              type="password"
              className="infoInput"
              placeholder="Enter Password"
              name="password"
              ref={password}
            />
          </div> 
  
          <div>
              <span style={{ fontSize: "12px" }}>
                Don't have an account
                <Link to='/SignUp'>Sign up</Link> 
              </span>
            <button className="button infoButton">Login</button>
          </div>
        </form>
      </div>
      {/* logIn */}
      <ToastContainer />
    </div>
  );
};


export default Auth;
