import React,{useRef,useEffect} from "react";
import "./Auth.css";
import Logo from "../../img/logo.png";
import { useSelector,useDispatch } from "react-redux";
import { Link,useNavigate } from "react-router-dom";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { register } from "../../Store/UserActions";
import { userActions } from "../../Store/UserReducer";

const Auth = () => {
    const dispatch = useDispatch();
  const email = useRef();
  const password = useRef();
  const name = useRef();
  const navigate = useNavigate();

  const { error, isAuthenticated } = useSelector((state) => state.user);

  const notify = (error) => toast.error(error);

  console.log(isAuthenticated);
  useEffect(() => {
    if (error) { //if error come
      notify(error);
      dispatch(userActions.clearError());// for clearing the error
    };
    if (isAuthenticated) {
        console.log('hello');
      navigate(`/profile`);
    }
  }, [isAuthenticated, navigate, error,dispatch]);

  //handling the logIn form 
  const submitHandler = (e) => {
    e.preventDefault();
    let data = { email: email.current.value, password: password.current.value,name:name.current.value};
    dispatch(register(data));
    email.current.value = "";
    password.current.value = "";
  }
  return (
    <div className="Auth">
      <div className="a-left">
        <img src={Logo} alt="" />
        <div className="Webname">
          <h1>ZKC Media</h1>
          <h6>Explore the ideas throughout the world</h6>
        </div>
      </div>
      {/* SignUp */}
      <div className="a-right">
        <form className="infoForm authForm" onSubmit={submitHandler}>
          <h3>Sign up</h3>
  
          <div>
            <input
              type="text"
              placeholder="First Name"
              className="infoInput"
              name="firstname"
            />
            <input
              type="text"
              placeholder="Last Name"
              className="infoInput"
              name="lastname"
            />
          </div>
  
          <div>
            <input
              type="text"
              className="infoInput"
              name="username"
              placeholder="Usernames"
              ref={name}
            />
          </div>
  
          <div>
            <input
              type="password"
              className="infoInput"
              name="password"
              placeholder="Password"
              ref={password}
            />
            <input
              type="text"
              className="infoInput"
              placeholder="Email"
              ref={email}
            />
          </div>
  
          <div>
              <span style={{fontSize: '12px'}}>Already have an account.
              <Link to='/logIn'>LogIn</Link>
              </span>
          </div>
          <button className="button infoButton" type="submit">Signup</button>
        </form>
      </div>
      {/* SignUp */}
      <ToastContainer></ToastContainer>
    </div>
  );
};

  export default Auth;