import { createAsyncThunk } from "@reduxjs/toolkit";
import axios from 'axios';

const loggedIn = createAsyncThunk("post/postuserDetail", async (data,{rejectWithValue}) => {
    try {
        const config={headers:{"Content-Type":"application/json"}};
        const response = await axios.post(`http://localhost:8000/api/logIn`,data,config);
        return response.data;
    }catch(error) {
        return rejectWithValue(error.response.data.message);
    }
});

const register = createAsyncThunk("create/createuser", async (data,{rejectWithValue}) => {
    try {
        const config={headers:{"Content-Type":"application/json"}};
        const response = await axios.post(`http://localhost:8000/api/Register`,data,config);
        return response.data;
    }catch(error) {
        return rejectWithValue(error.response.data.message);
    }
});

const updateProfile = createAsyncThunk("update/updateUserprofile", async (data,{rejectWithValue}) => {
    try {
        const config={headers:{"Content-Type":"multipart/form-data"},withCredentials:true};
        const response = await axios.put(`http://localhost:8000/api/updateProfile`,data,config);
        return response.data;
    }catch(error) {
        return rejectWithValue(error.response.data.message);
    }
});

const logOut = createAsyncThunk("logOut/logOutUser", async (data,{rejectWithValue}) => {
    try {
        const config={headers:{"Content-Type":"application/json"},withCredentials:true};
        const response = await axios.get(`http://localhost:8000/api/logOut`,config);
        return response.data;
    }catch(error) {
        return rejectWithValue(error.response.data.message);
    }
});


export {loggedIn,register,updateProfile,logOut}