import { createSlice } from "@reduxjs/toolkit";
import { loggedIn,register,updateProfile,logOut} from "./UserActions";

const userSlice = createSlice({
    name: 'User',
    initialState:{ user: [], loading: true, error: false, isAuthenticated: false,isUpdated:false },
    reducers: {
        clearError(state, action) {
            state.error = false;
        },
        changeUpdate(state,action){
            state.isUpdated=false;
        }
    },
    extraReducers: {
        //loggedIn
        [loggedIn.pending]: (state, action) => {
            state.loading = true;
        },
        [loggedIn.fulfilled]: (state, action) => {
            state.loading = false;
            state.user = action.payload.user;
            state.error = false;
            state.isAuthenticated = true;
        },
        [loggedIn.rejected]: (state, action) => {
            state.loading = false;
            state.error = action.payload;
        },
        [register.pending]: (state, action) => {
            state.loading = true;
        },
        [register.fulfilled]: (state, action) => {
            state.loading = false;
            state.user = action.payload.user;
            state.error = false;
            state.isAuthenticated = true;
        },
        [register.rejected]: (state, action) => {
            state.loading = false;
            state.error = action.payload;
        },
        [updateProfile.pending]: (state, action) => {
            state.loading = true;
        },
        [updateProfile.fulfilled]: (state, action) => {
            state.loading = false;
            state.user = action.payload.user;
            state.error = false;
            state.isAuthenticated = true;
        },
        [updateProfile.rejected]: (state, action) => {
            state.loading = false;
            state.error = action.payload;
        },
        [logOut.pending]: (state, action) => {
            state.loading = true;
        },
        [logOut.fulfilled]: (state, action) => {
            state.loading = false;
            state.user = {};
            state.error = false;
            state.isAuthenticated = false;
        },
        [updateProfile.rejected]: (state, action) => {
            state.loading = false;
            state.error = action.payload;
        },
    
    }
});  

const userActions = userSlice.actions;

export {userSlice,userActions}