import { Modal, useMantineTheme } from "@mantine/core";
import { useRef,useState } from "react";
import { useDispatch} from "react-redux";
import { updateProfile } from "../../Store/UserActions";
import 'react-toastify/dist/ReactToastify.css';



function ProfileModal({ modalOpened, setModalOpened }) {
  const theme = useMantineTheme();
  const firstName = useRef();
  const lastName = useRef();
  const workAt = useRef();
  const livesIn = useRef();
  const relationShipStatus = useRef();
  const country = useRef();
  const [profile,setProfile]= useState();
  const [cover,setCover]= useState();

  const dispatch = useDispatch();

 //for handling the imageFile
 const fileHandler1=(e)=>{
  const reader=new FileReader();
  reader.onload=()=>{
      if(reader.readyState===2){
          setProfile(reader.result);
      }
  }
  reader.readAsDataURL(e.target.files[0]);
}

//for handling the imageFile
const fileHandler2=(e)=>{
  const reader=new FileReader();
  reader.onload=()=>{
      if(reader.readyState===2){
          setCover(reader.result);
      }
  }
  reader.readAsDataURL(e.target.files[0]);
}

  function submitHandler(e){
    e.preventDefault();
    let form = new FormData();
    
    if(firstName.current.value.length>0){
      console.log('heelloo');
         form.set("firstName",firstName.current.value);
    };
    if(lastName.current.value.length>0){
      form.set("lastName",lastName.current.value);
    };
    if(workAt.current.value.length>0){
      form.set("workAt",workAt.current.value);
    };
    if(livesIn.current.value.length>0){
      form.set("livesIn",livesIn.current.value);
    };
    if(relationShipStatus.current.value.length>0){
      form.set("relationShipStatus",relationShipStatus.current.value);
    };
    if(country.current.value.length>0){
      form.set("country",country.current.value);
    };
    if(profile){
      form.append("profile",profile);
    };
    if(cover){
      form.append("cover",cover);
    };
    dispatch(updateProfile(form));
    firstName.current.value="";
    lastName.current.value="";
    workAt.current.value="";
    livesIn.current.value="";
    relationShipStatus.current.value="";
    country.current.value="";


  }
 

  return (
    <Modal
      overlayColor={
        theme.colorScheme === "dark"
          ? theme.colors.dark[9]
          : theme.colors.gray[2]
      }
      overlayOpacity={0.55}
      overlayBlur={3}
      size="55%"
      opened={modalOpened}
      onClose={() => setModalOpened(false)}
    >
      <form className="infoForm" onSubmit={submitHandler}>
        <h3>Your info</h3>

        <div>
          <input
            type="text"
            className="infoInput"
            name="FirstName"
            placeholder="First Name"
            ref={firstName}
          />

          <input
            type="text"
            className="infoInput"
            name="LastName"
            placeholder="Last Name"
            ref={lastName}
          />
        </div>

        <div>
          <input
            type="text"
            className="infoInput"
            name="worksAT"
            placeholder="Works at"
            ref={workAt}
          />
        </div>

        <div>
          <input
            type="text"
            className="infoInput"
            name="livesIN"
            placeholder="LIves in"
            ref={livesIn}
          />

          <input
            type="text"
            className="infoInput"
            name="Country"
            placeholder="Country"
            ref={country}
          />
        </div>

        <div>
          <input
            type="text"
            className="infoInput"
            placeholder="RelationShip Status"
            ref={relationShipStatus}
          />
        </div>


        <div>
            Profile Image 
            <input type="file" name='profileImg' onChange={fileHandler1} />
            Cover Image
            <input type="file" name="coverImg" onChange={fileHandler2} />
        </div>

        <button className="button infoButton" >Update</button>
      </form>
    </Modal>
  );
}

export default ProfileModal;
